import React from "react"
import { Link, useStaticQuery, graphql } from "gatsby"

import "../styles/bio.scss"

const Bio = () => {
  const data = useStaticQuery(graphql`
    query BioQuery {
      site {
        siteMetadata {
          author {
            name
            work
            place
          }
          social {
            email
          }
        }
      }
    }
  `)

  const { author, social } = data.site.siteMetadata
  return (
    <section className="bio">
      <p>
        Hi, Im {author.name}! Ive been working as a UI/UX designer at {author.work} in {author.place}.
        Say hi, <a href="">{social.email}</a>
      </p>
    </section>
  )
}

export default Bio
