import React from "react"
import { Link, graphql } from "gatsby"

import "../styles/layout.scss"

const Layout = ({ data, location, children }) => {
  const rootPath = `${__PATH_PREFIX__}/`
  let header

  console.clear()
  console.log(data)
  
  const email = ''
  const linkedin = ''
  const instagram = ''
  const savee = ''

  if (location.pathname === rootPath) {
    header = (
      <Link to={`/about`}>
        About
      </Link>
    )
  } else {
    header = (
      <Link to={`/`}>
          Back
      </Link>
    )
  }
  
  return (
    <div>
      <header className="nav">
        {header}
        <nav>
          <ul className="social-menu">
            <li><a href="{email}">Email {email}</a></li>
            <li><a href="{linkedin}">Linkedin {linkedin}</a></li>
            <li><a href="{instagram}">Instagram {instagram}</a></li>
            <li><a href="{savee}">Savee {savee}</a></li>
          </ul>
        </nav>
      </header>

      <main>{children}</main>
      <footer>
         {new Date().getFullYear()}, Built with
        {` `}
        <a href="https://www.gatsbyjs.org">Gatsby</a>
      </footer>
    </div>
  )
}

export default Layout

export const pageQuery = graphql`
  query {
    site {
      siteMetadata {
        title
      }
    }
  }
`
